.header {
  border: 1px solid red;
  background-color: #15339b;
  position: fixed;
  top: 0;
  z-index: 10;
  width: 100%; }
  .header .header-div {
    display: flex;
    flex-direction: row;
    padding: 10px;
    justify-content: space-between;
    align-items: center; }

.hero {
  background: url("../images/slider-home.jpg") no-repeat;
  background-size: cover;
  background-position: center;
  height: 100vh;
  width: 100%;
  position: relative;
  top: 15px;
  display: flex;
  justify-content: space-around;
  align-items: center; }

.claim {
  text-align: center; }
  .claim span {
    color: #ffffff;
    display: block;
    text-align: center;
    font-weight: 300; }
  .claim span:first-child:before {
    content: "-";
    color: #de25e0;
    font-weight: 200Px;
    padding: 0 10px; }
  .claim span:first-child:after {
    content: "-";
    color: #de25e0;
    font-weight: 200Px;
    padding: 0 10px; }

.top-section {
  border: 1px solid red;
  height: 40vh;
  text-align: center; }

.what-we-do {
  text-transform: uppercase;
  color: #15339b;
  align-items: center;
  padding: 35px; }

.h3 {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-bottom: 10px; }

@media all and (min-width: 1200px) {
  .h3 {
    flex-direction: row; } }

.main {
  border: 1px solid red; }

.modern-design {
  border: 1px solid grey;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 15px;
  height: 35vh;
  width: 30vh;
  padding: 20px; }
  .modern-design h3 {
    padding-bottom: 15px; }
  .modern-design p {
    padding-bottom: 30px; }
  .modern-design h4 {
    padding-bottom: 15px; }

.main-div {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }

@media all and (min-width: 768px) {
  .main-div {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between; }
  .modern-design {
    padding: 15px; } }

@media all and (min-width: 1200px) {
  .main-div {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-content: center; }
  .modern-design {
    height: 45vh;
    width: 30vh;
    padding: 20px;
    margin: 15px; } }

.footer {
  background-color: black;
  color: white;
  height: 50vh;
  padding: 15px; }

.contact {
  font-weight: 600;
  padding: 20px 0; }

.contact-information span {
  display: block;
  padding-bottom: 10px; }

.footer--social-media {
  padding-top: 20px; }

.rrss {
  padding: 5px; }

.footer-links {
  padding: 15px;
  margin: auto;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding-top: 25px; }
  .footer-links li {
    padding-right: 10px; }

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  text-decoration: none;
  color: inherit;
  list-style-type: none; }

.wrapper {
  max-width: 1280px;
  margin: auto;
  padding: 25px;
  width: 100%; }

html {
  font-family: "Source Sans Pro", sans-serif;
  font-size: 16; }
